import React, {CSSProperties} from 'react';
import {Container, Row, Col, Navbar, NavbarBrand, Nav, NavItem, NavLink} from 'reactstrap';

import branding from '../assets/branding.png';
import productsBG from '../assets/products_bg.jpg';
import itTalents from '../assets/it_talents.jpg';
import farmingEquipment from '../assets/farming_equipment.jpg';
import covid19Products from '../assets/covid19_products.jpg';
import secondHandedHeavyMachinery from '../assets/2nd_handed_heavy_machinery.jpg';
import experienceBG from '../assets/experience_bg.jpg';
import japanFlag from '../assets/japan_flag.png';
import ukFlag from '../assets/uk_flag.png';
import usFlag from '../assets/us_flag.png';
import meFlags from '../assets/me_flags.png';
import contactUsBG from '../assets/contact_us_bg.jpg';

const SECTION_HEIGHT = 450;
const HEADER_HEIGHT = 120;
const SECTION_STYLES:CSSProperties = {
  height: `${SECTION_HEIGHT}px`,
  textAlign: 'center',
  display: 'table-cell',
  verticalAlign: 'middle',
}
const FOOTER_STYLES:CSSProperties = {
  height: 150,
  backgroundColor: 'rgb(19, 27, 77)',
  color: 'gray',
};


function LandingPage() {
  return (
    <div className='app'>
      <header className='app-header' style={{position: 'fixed', width: '100%', top: '0', zIndex: 10, height: `${HEADER_HEIGHT}px`}}>
        <Container>
          <Row>
            <Col>
              <Navbar>
                <NavbarBrand>
                  <img src={branding} alt='WHOLEHEARTED' style={{height: `calc(${HEADER_HEIGHT}px - 2rem)`}} />
                </NavbarBrand>
                <Nav>
                  <NavItem><NavLink href='#vision'>VISION</NavLink></NavItem>
                  <NavItem><NavLink href='#products'>PRODUCTS</NavLink></NavItem>
                  <NavItem><NavLink href='#experience'>EXPERIENCE</NavLink></NavItem>
                  <NavItem><NavLink href='#contact-us'>CONTACT US</NavLink></NavItem>
                </Nav>
              </Navbar>
            </Col>
          </Row>
        </Container>
      </header>
      <main>
        <div style={{height: `${HEADER_HEIGHT}px`}}>
          &nbsp;
        </div>
        <Container id='vision'>
          <Row style={{padding: '3rem 0'}}>
            <Col style={{display: 'table'}}>
              <section style={SECTION_STYLES}>
                <p style={{fontSize: '5rem'}}>Vision</p>
                <p style={{fontSize: '2rem'}}>To connect qualified Korean products to the global market</p>
              </section>
            </Col>
          </Row>
        </Container>

        <div id='products' style={{backgroundImage: `url(${productsBG})`, backgroundPosition: 'center', backgroundSize: 'cover'}}>
          <div style={{backgroundColor: 'rgba(255, 255, 255, 0.4)'}}>
            <Container>
              <Row style={{padding: '3rem 0'}}>
                <Col style={{display: 'table', backgroundColor: 'rgba(255, 255, 255, 0.8)'}}>
                  <section style={SECTION_STYLES}>
                    <Row>
                      <Col>
                        <div style={{
                          width: '150px',
                          height: '150px',
                          background: `url(${itTalents})`,
                          backgroundSize: 'cover',
                          borderRadius: '75px',
                          margin: 'auto',
                        }} />
                        <br/>
                        <h5>IT Talents</h5>
                      </Col>

                      <Col>
                        <div style={{
                          width: '150px',
                          height: '150px',
                          background: `url(${farmingEquipment})`,
                          backgroundSize: 'cover',
                          borderRadius: '75px',
                          margin: 'auto',
                        }} />
                        <br/>
                        <h5>Farming Equipment</h5>
                      </Col>

                      <Col>
                        <div style={{
                          width: '150px',
                          height: '150px',
                          background: `url(${covid19Products})`,
                          backgroundSize: 'cover',
                          borderRadius: '75px',
                          margin: 'auto',
                        }} />
                        <br/>
                        <h5>COVID-19 Products</h5>
                      </Col>

                      <Col>
                        <div style={{
                          width: '150px',
                          height: '150px',
                          background: `url(${secondHandedHeavyMachinery})`,
                          backgroundSize: 'cover',
                          borderRadius: '75px',
                          margin: 'auto',
                        }} />
                        <br/>
                        <h5>2<sup>nd</sup> handed<br/>heavy machinery</h5>
                      </Col>
                    </Row>
                  </section>
                </Col>
              </Row>
            </Container>
          </div>
        </div>

        <div id='experience' style={{backgroundImage: `url(${experienceBG})`, backgroundPosition: 'center', backgroundSize: 'cover'}}>
          <div style={{backgroundColor: 'rgba(255, 255, 255, 0.4)'}}>
            <Container>
              <Row style={{padding: '3rem 0'}}>
                <Col style={{display: 'table'}}>
                  <section style={SECTION_STYLES}>
                    <br/>
                    <Row>
                      <Col>
                        <img src={japanFlag} alt='JAPAN FLAG' style={{width: '350px'}} />
                      </Col>
                      <Col>
                        <img src={ukFlag} alt='UK FLAG' style={{width: '350px'}} />
                      </Col>
                      <Col />
                    </Row>
                    <br/>
                    <Row>
                      <Col>
                        <img src={usFlag} alt='US FLAG' style={{width: '350px'}} />
                      </Col>
                      <Col>
                        <img src={meFlags} alt='MIDDLE EAST FLAGS' style={{width: '350px'}} />
                      </Col>
                      <Col />
                    </Row>
                    <br/>
                  </section>
                </Col>
              </Row>
            </Container>
          </div>
        </div>

        <div id='contact-us' style={{backgroundImage: `url(${contactUsBG})`, backgroundPosition: 'center', backgroundSize: 'cover'}}>
          <div style={{backgroundColor: 'rgba(255, 255, 255, 0.4)'}}>
            <Container>
              <Row>
                <Col style={{display: 'table'}}>
                  <section style={{...SECTION_STYLES, height: '1000px'}}>
                    <h2><a href='mailto:info@wholehearted.kr' style={{color: 'rgb(19, 27, 77)'}}>info@wholehearted.kr</a></h2>
                  </section>
                </Col>
              </Row>
            </Container>
          </div>
        </div>


      </main>
      <footer style={FOOTER_STYLES}>
        <Container>
          <Row>
            <Col>
              <br/>
              Registration Number: 226-86-01361<br/>
              Company: whole hearted<br/>
              Address: Seoul, Kangseo-Gu, Yangcheon-Ro 482, 102<br/>
              Tel: +82-10-4219-5025
            </Col>
          </Row>
        </Container>

      </footer>
    </div>
  );
}

export default LandingPage;
